@import "sass-rem";

.statisticsOne {
    width: 100%;
    padding: rem(26px 0);
}

.headerList {
    display: flex;
    gap: rem(10px);
    margin-bottom: rem(10px);
    padding-left: rem(20px);
}

.headerItemBtn {
    padding: rem(10px 20px);
    font-weight: 400;
    font-size: rem(14px);
    line-height: rem(16px);
    color: #fff;
    transition: .3s;
    border-radius: rem(20px);
    background: #000;
    cursor: pointer;

    &:hover,
    &.headerItemBtnActive {
        background: rgba(255, 255, 255, 0.12);
    }
}

.tableName {
    font-weight: 500;
    font-size: rem(18px);
    line-height: rem(21px);
    text-transform: uppercase;
    color: #fff;
    padding: rem(14px 24px);
    background: #262626;
}

.tableRow {
    display: flex;
    align-items: center;
    padding: rem(10px);
    border-bottom: 1px solid rgba(110, 110, 110, 0.5);
}

.tableCell {
    font-weight: 500;
    font-size: rem(18px);
    line-height: rem(21px);
    color: #fff;

    &.tableCellLive {
        color: #000;
        background: #F7FF13;

        &:nth-child(5) {
            color: #000;
            background: #F7FF13;
        }
    }

    &:nth-child(1) {
        font-weight: 700;
        text-align: right;
        margin-right: rem(20px);
        width: rem(38px);
        min-width: rem(38px);
        max-width: rem(38px);
        flex-grow: 0;
    }

    &:nth-child(2) {
        flex-grow: 1;
        margin-right: rem(20px);
    }

    &:nth-child(3) {
        margin-right: rem(16px);
    }

    &:nth-child(4) {
        margin-right: rem(16px);
        padding-left: rem(25px);
        position: relative;

        &:before {
            content: "";
            position: absolute;
            top: 50%;
            left: 0;
            width: rem(17px);
            height: rem(17px);
            transform: translateY(-50%);
            background: url("../../assets/images/svg/statistics-time.svg") no-repeat 50% 50% /contain;
        }
    }

    &:nth-child(5) {
        margin-right: rem(16px);
        width: rem(130px);
        height: rem(30px);
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
        padding: rem(7px 14px);
        background: rgba(110, 110, 110, 0.5);
        text-transform: uppercase;
        font-size: rem(14px);
        line-height: rem(16px);
    }
}