 *:where(:not(html, iframe, canvas, img, svg, video, audio):not(svg *, symbol *)) {
	all: unset;
	display: revert;
}

*,
*::before,
*::after {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
	-webkit-tap-highlight-color: rgba(0,0,0,0);
	-webkit-tap-highlight-color: transparent;
}

html {
	overflow-x: hidden;
	max-width: 100%;
}

a, button {
	cursor: revert;
}

ol, ul, menu {
	list-style: none;
}

img {
	max-inline-size: 100%;
	max-block-size: 100%;
}

table {
	border-collapse: collapse;
}

textarea {
	white-space: revert;
}

::placeholder {
	color: unset;
}

::marker {
	content: "";
}

:where([hidden]) {
	display: none;
}

:where([contenteditable]:not([contenteditable="false"])) {
	-moz-user-modify: read-write;
	-webkit-user-modify: read-write;
	overflow-wrap: break-word;
}

:where([draggable="true"]) {
	-webkit-user-drag: element;
}

:where(dialog:modal) {
	all: revert;
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active{
	-webkit-box-shadow:0 0 0 30px #27304c inset !important;
}
input:-webkit-autofill{
	-webkit-text-fill-color: #fff !important;
}

html,
body {
	font-family: 'Roboto Condensed', Arial, Helvetica, sans-serif;
	background: #000;
	overflow-x: hidden;
	max-width: 100%;
	font-size: calc(100vw / 120);
	@media (max-width: 1024px) {
		font-size: calc(100vw / 90);
	}
	@media (max-width: 768px) {
		font-size: calc(100vw / 60);
	}
	@media (max-width: 480px) {
		font-size: calc(100vw / 45);
	}
}

main {
	position: relative;
	z-index: 1;
}

.container {
	width: 100%;
	max-width: 1750px;
	padding: 0 20px;
	margin: 0 auto;
}

.visually-hidden {
	position: absolute;
	width: 1px;
	height: 1px;
	margin: -1px;
	border: 0;
	padding: 0;
	white-space: nowrap;
	clip-path: inset(100%);
	clip: rect(0 0 0 0);
	overflow: hidden;
}

.visually-hidden-mob {
	@media (max-width: 768px) {
		position: absolute;
		width: 1px;
		height: 1px;
		margin: -1px;
		border: 0;
		padding: 0;
		white-space: nowrap;
		clip-path: inset(100%);
		clip: rect(0 0 0 0);
		overflow: hidden;
	}
}

.no-click {
	pointer-events: none;
}

.no-scroll {
	overflow: hidden;
}

.header {
    background: #000;
}

.page {
	position: fixed;
	top: rem(146px);
	bottom: 0;
	right: 0;
	left: 0;
	max-height: calc(100vh - #{rem(145px)});
	display: flex;
	z-index: 2;
	overflow-x: hidden;

	&--index {
		max-width: rem(1542px);
		padding-left: rem(100px);
		padding-right: rem(5px);
		flex-direction: column;
	}

	&--profile {
		max-width: rem(1482px);
		padding-left: rem(20px);
		padding-right: rem(10px);
		margin: 0 auto;
		gap: rem(31px);
	}

	&--apps {
		top: rem(80px);
		max-height: calc(100vh - #{rem(79px)});
		background-image: url("../../images/apps-bg-x2.min.png");
		background-repeat: no-repeat;
		background-size: rem(1225px) rem(786px);
		background-position: 0 rem(100px);
	}

	&--events {
		padding-left: rem(86px);
		overflow: hidden;
		gap: rem(20px);
		max-width: rem(1540px);
	}

	&--statistics-one,
	&--statistics-five {
		padding-left: rem(88px);
		overflow-y: auto;
		max-width: 100%;
		padding-right: rem(25px);
		display: block;
	}

	&--statistics-two,
	&--statistics-six {
		display: block;
	}

	&--site-map,
	&--statistics-three,
	&--statistics-four {
		padding-left: rem(25px);
		overflow-y: auto;
		max-width: 100%;
		padding-right: rem(25px);
	}

	&--game {
		overflow: hidden;
		max-width: rem(1542px);
		padding-left: rem(86px);
		gap: rem(20px);
	}

	&--site-map {
		display: block;
		padding-bottom: rem(50px);
	}

	&--faq-big-list {
		display: block;
		overflow-y: auto;
		padding: rem(60px 20px 20px);
	}

	&--requests {
		display: flex;
		gap: rem(20px);
		overflow-y: auto;
		padding: rem(0 20px 20px);
	}

	&--statistics-search {
		display: block;
	}
}

.aside {
	position: fixed;
    top: 9.125rem;
    bottom: 0;
    right: 0;
    max-height: calc(100vh - 9.0625rem);
	padding: rem(0 15px 15px);
	overflow-y: auto;

	&--index {
		width: rem(373px);
		display: flex;
		flex-direction: column;
		gap: rem(20px);
	}

	&--events {
		width: rem(380px);
		display: flex;
		flex-direction: column;
	}
}

section,
.select {
	position: relative;
}

.scroll {
	&::-webkit-scrollbar {
		width: rem(5px);
	}
	 
	&::-webkit-scrollbar-track {
		background-color: #28293e;
		border-radius: rem(5px);
	}
	
	&::-webkit-scrollbar-thumb {
		border-radius: rem(5px);
		background: #8b8b97;
	}
}

.scroll-horizontal {
	&::-webkit-scrollbar {
		height: rem(5px);
	}
	 
	&::-webkit-scrollbar-track {
		background-color: #28293e;
		border-radius: rem(5px);
	}
	
	&::-webkit-scrollbar-thumb {
		border-radius: rem(5px);
		background: #8b8b97;
		cursor: pointer;
	}
}
