@import "sass-rem";

.choiceSport {
	width: 100%;
	padding: rem(25px);
}

.wrapper {
	width: 100%;
	max-width: rem(1250px);
	margin: 0 auto;
	padding-top: rem(25px);
}

.title {
	font-weight: 500;
	font-size: rem(20px);
	line-height: rem(32px);
	color: #fff;
	margin-bottom: rem(50px);
}

.list {
	display: flex;
	gap: rem(10px);
	flex-wrap: wrap;
}

.item {
	width: rem(130px);
	height: rem(120px);
}

.link {
	display: flex;
	flex-direction: column;
	align-items: center;
	width: rem(130px);
	height: rem(130px);
	padding: rem(20px 10px 10px);

	&:hover {
		background: #F7FF13;

		.linkName {
			color: #000;
		}

		.linkIcon {
			&:before {
				opacity: 1;
			}
		}
	}
}

.linkIcon {
	width: rem(40px);
	height: rem(40px);
	margin-bottom: rem(21px);
	position: relative;
	background-repeat: no-repeat;
	background-size: contain;
	background-position: 50% 50%;

	&:before {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		background-repeat: no-repeat;
		background-size: contain;
		background-position: 50% 50%;
		opacity: 0;
	}
}

.linkIconFootball {
	background-image: url("../../assets/images/svg/sports/football.svg");

	&:before {
		background-image: url("../../assets/images/svg/sports/football-hover.svg");
	}
}

