.live {
	&:after {
		content: "";
		width: rem(16px);
		height: rem(16px);
		position: absolute;
		top: rem(-4px);
		right: rem(-4px);
		background: url("../../assets/images/svg/menu-live.svg") no-repeat 50% 50% /contain;
	}
}

.more {
	position: fixed;
	bottom: 0;
	left: 0;
	width: 100%;
	height: rem(40px);
	background: #232323;
	font-size: 0;
	width: rem(86px);

	&:before {
		content: "";
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		width: rem(24px);
		height: rem(24px);
		background: url("../../assets/images/svg/menu-more.svg") no-repeat 50% 50% /contain;
		opacity: .6;
		transition: .3s;
	}

	&:hover {
		&:before {
			opacity: 1;
		}
	}
}

