.statistics-five {
    width: 100%;
    max-width: rem(1442px);
    padding: rem(26px 15px);

    &__list {
        background: #151515;
        gap: rem(10px);
    }

    &__item {
        width: 100%;
    }

    &__header-row {
        background: #262626;
        position: relative;
        display: flex;
        align-items: center;
        padding: rem(14px 20px 13px 25px);

        &:before {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            bottom: 0;
            background: #1D7E3A;
            width: rem(3px);
        }
    }

    &__header-cell {
        font-weight: 400;
        font-size: rem(14px);
        line-height: rem(16px);
        text-align: center;
        color: #7E7E7E;
        margin-right: 2px;

        &:nth-child(1) {
            font-weight: 500;
            font-size: rem(18px);
            line-height: rem(21px);
            color: #FFFFFF;
            text-transform: uppercase;
            text-align: left;
            flex-grow: 1;
        }

        &:nth-child(2),
        &:nth-child(3),
        &:nth-child(4),
        &:nth-child(5),
        &:nth-child(6),
        &:nth-child(7),
        &:nth-child(8),
        &:nth-child(10),
        &:nth-child(11),
        &:nth-child(12),
        &:nth-child(13),
        &:nth-child(14) {
            width: rem(48px);
        }

        &:nth-child(4),
        &:nth-child(7),
        &:nth-child(10),
        &:nth-child(11),
        &:nth-child(13) {
            margin-right: rem(10px);
        }

        &:nth-child(9) {
            width: rem(24px);
        }
        &:nth-child(14) {
            width: rem(40px);
        }
    }

    &__header-cell-btn {
        display: block;
        width: rem(40px);
        height: rem(20px);
        cursor: pointer;
        position: relative;

        &:before {
            content: "";
            position: absolute;
            width: rem(14px);
            height: rem(12px);
            right: rem(5px);
            top: 50%;
            transform: translateY(-50%);
            background: url("../../images/svg/statistics-triangle-down.svg") no-repeat 50% 50% /contain;
        }
    }

    &__row {
        background: #151515;
        position: relative;
        display: flex;
        align-items: center;
        padding: rem(9px 20px 9px 25px);
        border-bottom: 1px solid rgba(255, 255, 255, 0.15);

        &:nth-child(2) {
            .statistics-five__cell {
                &:nth-child(1) {
                    position: relative;
                    &:before {
                        content: "";
                        position: absolute;
                        width: rem(14px);
                        height: rem(12px);
                        left: 0;
                        top: 50%;
                        transform: translateY(-50%);
                        background: url("../../images/svg/statistics-triangle-down.svg") no-repeat 50% 50% /contain;
                    }
                }
            }
        }

        &:nth-child(3) {
            .statistics-five__cell {
                &:nth-child(1) {
                    text-transform: none;
                }
            }
        }

        &:nth-child(4) {
            .statistics-five__cell {
                &:nth-child(1) {
                    padding-left: rem(53px);
                    text-transform: none;
                    position: relative;
                    &:before {
                        content: "";
                        position: absolute;
                        width: rem(14px);
                        height: rem(12px);
                        left: rem(26px);
                        top: 50%;
                        transform: translateY(-50%);
                        background: url("../../images/svg/statistics-triangle-down.svg") no-repeat 50% 50% /contain;
                    }
                }
            }
        }

        &:last-child {
            border: none;
        }
    }

    &__cell {
        font-weight: 700;
        font-size: rem(16px);
        line-height: rem(18px);
        text-align: center;
        color: #fff;
        margin-right: 2px;
        padding: rem(10px 0);
        background: #262626;

        &:nth-child(1) {
            padding-left: rem(26px);
            position: relative;
            font-weight: 400;
            font-size: rem(16px);
            line-height: rem(18px);
            color: #939393;
            background: none;
            text-transform: uppercase;
            text-align: left;
            flex-grow: 1;
        }

        &:nth-child(2),
        &:nth-child(3),
        &:nth-child(4),
        &:nth-child(5),
        &:nth-child(6),
        &:nth-child(7),
        &:nth-child(8),
        &:nth-child(10),
        &:nth-child(11),
        &:nth-child(12),
        &:nth-child(13) {
            width: rem(48px);
        }

        &:nth-child(4),
        &:nth-child(7),
        &:nth-child(10),
        &:nth-child(11),
        &:nth-child(13) {
            margin-right: rem(10px);
        }

        &:nth-child(9) {
            background: none;
            width: rem(24px);
        }

        &:nth-child(14) {
            width: rem(40px);
            background: none;
            text-align: right;
            font-weight: 400;
            color: #9E9E9E;
        }

        &--disable {
            background: #1F1F1F;
            color: #A7A7A7;
            position: relative;

            &:before {
                content: "";
                position: absolute;
                top: 0;
                left: 0;
                bottom: 0;
                width: 1px;
                background: #2E2E2E;
            }
        }

        &--btn-wrapper {
            display: flex;
            flex-direction: column;
            height: rem(38px);
            gap: rem(4px);
            padding: rem(2px 0);
        }
    }

    &__cell-btn {
        display: block;
        width: rem(20px);
        height: rem(15px);
        background: url("../../images/svg/statistics-triangle-up.svg") no-repeat 50% 50%;
        background-size: rem(14px 12px);
        cursor: pointer;

        &:nth-child(2) {
            background-image: url("../../images/svg/statistics-triangle-down.svg");
        }
    }
}
