@import "sass-rem";

.balance {
	background: radial-gradient(63.59% 100% at 50% 100%, #494B02 0%, rgba(69, 71, 13, 0.571) 41.15%, rgba(49, 50, 9, 0.34) 72.92%, rgba(79, 82, 10, 0) 100%);
	margin-bottom: rem(10px);
	padding: rem(15px 15px 20px);
	position: relative;

	&:before {
		position: absolute;
		content: "";
		left: 0;
		right: 0;
		width: 100%;
		height: rem(4px);
		bottom: 0;
		background: radial-gradient(50% 50% at 50% 50%, #F7FF13 0%, rgba(0, 0, 0, 0) 100%);
	}
}    

.header {
	display: flex;
	margin-bottom: rem(21px);
}

.btnCopy {
	position: relative;
	font-weight: 400;
	font-size: rem(14px);
	line-height: rem(16px);
	color: #fff;
	padding-right: rem(22px);
	margin-right: auto;
	display: inline-block;
	white-space: nowrap;
	cursor: pointer;

	&:before {
		content: "";
		position: absolute;
		top: 50%;
		right: 0;
		transition: .3s;
		opacity: .6;
		width: rem(14px);
		height: rem(14px);
		background: url("../../assets/images/svg/balance-copy.svg") no-repeat 50% 50% /contain;
		transform: translateY(-50%);
	}

	&:hover {
		&:before {
			opacity: 1;
		}
	}
}

.btnUpdate,
.btnShow {
	position: relative;
	font-size: 0;
	line-height: 0;
	width: rem(16px);
	height: rem(16px);
	display: inline-block;
	cursor: pointer;

	&:before {
		content: "";
		position: absolute;
		top: 50%;
		right: 50%;
		transition: .3s;
		opacity: .6;
		width: rem(16px);
		height: rem(16px);
		background: url("../../assets/images/svg/balance-update.svg") no-repeat 50% 50% /contain;
		background-repeat: no-repeat;
		background-size: contain;
		background-position: 50% 50%;
		transform: translate(-50%, -50%);
	}

	&:hover {
		&:before {
			opacity: 1;
		}
	}
}

.btnUpdate {
	margin-right: rem(10px);
	margin-left: auto;
	&:before {
		background-image: url("../../assets/images/svg/balance-update.svg");
	}
}

.btnShow {
	margin-left: rem(5px);
	&:before {
		background-image: url("../../assets/images/svg/balance-show.svg");
	}
}

.count {
	font-weight: 500;
	font-size: rem(18px);
	line-height: rem(21px);
	color: #fff;
	margin-bottom: rem(5px);
}

.text {
	font-weight: 400;
	font-size: rem(12px);
	line-height: (14px);
	color: #828282;
	margin-bottom: rem(20px);
}

.btnWrapper {
	display: flex;
	gap: rem(10px);
	margin-bottom: rem(20px);
}

.btn {
	text-transform: uppercase;
	padding: rem(12px 18px);
	cursor: pointer;
	font-weight: 700;
	font-size: rem(14px);
	line-height: rem(16px);
	text-align: center;
	transition: .3s;
	border-radius: rem(3px);
}

.btnAdd {
	background: #FAFE13;
	color: #000;
}

.btnVerification {
	background: rgba(250, 254, 19, 0.15);
	color: #F7FF13;
}

.info {
	display: flex;
	gap: rem(45px);
}

.infoGame {
	display: flex;
	flex-direction: column;
	gap: rem(5px);
}

.infoCount {
	font-weight: 500;
	font-size: rem(18px);
	line-height: rem(21px);
	color: #fff;
}

.infoText {
	font-weight: 400;
	font-size: rem(12px);
	line-height: rem(14px);
	color: #828282;
}
